import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';

//css
import './App.css';
import './reset.css';

//components
import SideNav from './components/SideNav/SideNav';

//fonts
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { faHeart , faCamera , faUser , faHome , faDesktop , faBlog , faPhone , faEnvelope , faPhoneSquare} from '@fortawesome/free-solid-svg-icons';

library.add(fab , faHeart , faCamera , faHome , faUser , faDesktop , faBlog , faPhone , faEnvelope , faPhoneSquare)

class App extends Component {

  render() {
    return (
      <div>
        <Switch>
          <Route exact path="/" component={SideNav}/>
        </Switch>
      </div>
    );
  }
}

export default App;
