import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCamera , faHome , faUser , faDesktop , faBlog , faPhone} from '@fortawesome/free-solid-svg-icons'
import moment from 'moment'

//components
import Home from "../Home/Home";
import Contact from '../Contact/Contact';
import About from '../About/About';
import Skills from '../Skills/Skills';
import Gallery from '../Gallery/Gallery';
import Blog from '../Blog/Blog';

//css
import './SideNav.css'

class SideNav extends Component {

  constructor(){
    super()
     this.state={
      homeShow:true,
      aboutShow:false,
      skillShow:false,
      blogShow:false,
      GalleryShow:false,
      contactShow:false,
      mobileNav:false,
      iconMenu:false
     }
  }

  componentDidMount() {
    setInterval( () => {
      this.setState({
        curTime : new Date().toLocaleString()
      })
    },1000)
  }

  render() {

    const mobileChange = () =>{
      this.setState({
        mobileNav:!this.state.mobileNav,
        iconMenu:!this.state.iconMenu
      })
    }

    const homeChange = () =>{
      this.setState({
        mobileNav:!this.state.mobileNav,
        iconMenu:!this.state.iconMenu,
        homeShow:true,
        aboutShow:false,
        skillShow:false,
        GalleryShow:false,
        blogShow:false,
        contactShow:false
      })
    }

    const aboutChange = () =>{
      this.setState({
        mobileNav:!this.state.mobileNav,
        iconMenu:!this.state.iconMenu,
        homeShow:false,
        aboutShow:true,
        skillShow:false,
        GalleryShow:false,
        blogShow:false,
        contactShow:false
      })
    }

    const skillChange = () =>{
      this.setState({
        mobileNav:!this.state.mobileNav,
        iconMenu:!this.state.iconMenu,
        homeShow:false,
        aboutShow:false,
        skillShow:true,
        GalleryShow:false,
        blogShow:false,
        contactShow:false
      })
    }

    const GalleryChange = () =>{
      this.setState({
        mobileNav:!this.state.mobileNav,
        iconMenu:!this.state.iconMenu,
        homeShow:false,
        aboutShow:false,
        skillShow:false,
        GalleryShow:true,
        blogShow:false,
        contactShow:false
      })
    }

    const blogChange = () =>{
      this.setState({
        mobileNav:!this.state.mobileNav,
        iconMenu:!this.state.iconMenu,
        homeShow:false,
        aboutShow:false,
        skillShow:false,
        GalleryShow:false,
        blogShow:true,
        contactShow:false
      })
    }

    const contactChange = () =>{
      this.setState({
        mobileNav:!this.state.mobileNav,
        iconMenu:!this.state.iconMenu,
        homeShow:false,
        aboutShow:false,
        skillShow:false,
        GalleryShow:false,
        blogShow:false,
        contactShow:true
      })
    }
    return (
        <div>
          <div className={`sideBar ${ this.state.mobileNav ? 'mobNavShow':'mobNavHide'}`}>
              {/* image and title */}
              <div className="profile-img">
                <img src={ require('../../assets/images/akshat_soni.jpg')}  alt="akshat soni"/>
              </div>
              <div className="site-title-block">
                <h1 className="site-title">
                  Akshat Soni
                </h1>
                <p className="site-description DateTimeHide">{ 
                  moment().format('ll, h:mm:ss a')
                }</p>
              </div>
              {/* // image and title */}
              {/* navigation */}
              <div className="site-nav">
                  <ul className="site-main-menu">
                    <li className="hvr-sweep-to-right">
                      <a href onClick={ homeChange }>
                      <FontAwesomeIcon icon={ faHome } />
                      {' '}{'\u00A0'}
                      Home</a>
                    </li>
                    <li className="hvr-sweep-to-right">
                      <a href onClick={ aboutChange }>
                      <FontAwesomeIcon icon={ faUser } />
                      {' '}{'\u00A0'}
                      About</a>
                    </li>
                    <li className="hvr-sweep-to-right">
                      <a href onClick={ skillChange }>
                      <FontAwesomeIcon icon={ faDesktop } />
                      {' '}{'\u00A0'}
                      Skills</a>
                    </li>
                    <li className="hvr-sweep-to-right" style={{textIndent:"15px"}}>
                      <a href onClick={ GalleryChange }>
                      <FontAwesomeIcon icon={ faCamera } />
                      {' '}{'\u00A0'}
                      Gallery</a>
                    </li>
                    <li className="hvr-sweep-to-right">
                      <a href onClick={ blogChange }>
                      <FontAwesomeIcon icon={ faBlog } />
                      {' '}{'\u00A0'}
                      Blog</a>
                    </li>
                    <li className="hvr-sweep-to-right" style={{textIndent:"20px"}}>
                      <a href onClick={ contactChange }>
                      <FontAwesomeIcon icon={ faPhone } />
                      {' '}{'\u00A0'}
                      Contact</a>
                    </li>
                  </ul>
              </div>
              <span className="navFooter">Powered by React<FontAwesomeIcon icon={['fab' , 'react']} /> | Made by Akshat<img src={ require('./heart-solid.svg')} style={{ paddingLeft:'3px' , width:'4%' }} alt="akshat soni"/></span>
              {/* end navigation */}
          </div>
          <div className="mobileNav">
              <div className="dateTime">
                { 
                  moment().format('ll, h:mm:ss a')
                }
              </div>
              <div className={`mobmenu__div ${ this.state.iconMenu ? 'change': ''}`} onClick={ mobileChange }>
                <div className="bar1"></div>
                <div className="bar2"></div>
                <div className="bar3"></div>
              </div>
          </div>
          <div className={`${ this.state.homeShow ? 'sectionShow' : 'sectionHide'}`}>
            <Home/>
          </div>
          <div className={`${ this.state.aboutShow ? 'sectionShow' : 'sectionHide'}`}>
            <About/>
          </div>
          <div className={`${ this.state.skillShow ? 'sectionShow' : 'sectionHide'}`} >
            <Skills/>
          </div>
          <div className={`${ this.state.GalleryShow ? 'sectionShow' : 'sectionHide'}`} >
            <Gallery/>
          </div>
          <div className={`${ this.state.blogShow ? 'sectionShow' : 'sectionHide'}`} >
            <Blog/>
          </div>
          <div className={`${ this.state.contactShow ? 'sectionShow' : 'sectionHide'}`}>
            <Contact/>
          </div>
          <div className="clearfix"></div>
        </div>
    );
  }
}

export default SideNav;
