import React, { Component } from 'react';
import Lightbox from 'react-images';
import Gallery from 'react-photo-gallery';

//styles
import './Gallery.css';

class myGallery extends Component {

  constructor () {
		super();

		this.state = {
			lightboxIsOpen: false,
			currentImage: 0,
		};

    this.closeLightbox = this.closeLightbox.bind(this);
    this.openLightbox = this.openLightbox.bind(this);
		this.gotoNext = this.gotoNext.bind(this);
		this.gotoPrevious = this.gotoPrevious.bind(this);
  }
    openLightbox(event, obj) {
      this.setState({
        currentImage: obj.index,
        lightboxIsOpen: true,
      });
    }
    gotoPrevious () {
      this.setState({
        currentImage: this.state.currentImage - 1,
      });
    }
    gotoNext () {
      this.setState({
        currentImage: this.state.currentImage + 1,
      });
    }
    closeLightbox () {
      this.setState({
        currentImage: 0,
        lightboxIsOpen: false,
      });
    }

  render() {

    const columns = (containerWidth) => {
      let columns = 2;
      if (containerWidth >= 500) columns = 3;
      if (containerWidth >= 900) columns = 3;
      if (containerWidth >= 1500) columns = 4;
      return columns;
    }

    const images = [{ 
      src: require('../../assets/images/akshat.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat1.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat2.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat3.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat4.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat5.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat6.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat_soni.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }, { 
      src: require('../../assets/images/akshat7.jpg'), 
      width:1,
      height:1,
      alt:"akshat soni web developer"
      }  
      ]


    return (
          <div className="akshat__rtdv">
            <div className="akshat__container akshat__gallerybg">
              <h1 className="akshat__heading">Gallery</h1>
                <Lightbox
                  images={ images }
                  isOpen={this.state.lightboxIsOpen}
                  onClickPrev={this.gotoPrevious}
                  onClickNext={this.gotoNext}
                  currentImage={this.state.currentImage}
                  onClose={this.closeLightbox}
                />
                < Gallery 
                  photos={ images } 
                  columns={ columns } 
                  onClick={ this.openLightbox }
                  margin={12} 
                />
            </div>
          </div>
    );
  }
}

export default myGallery;
