import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope , faPhoneSquare } from '@fortawesome/free-solid-svg-icons'
import axios from 'axios';

//style
import './Contact.css';


class Contact extends Component {

      constructor(props){
        super(props)
        this.state = {
            fullName:"",
            fullNameError:"",
            email:"",
            emailError:"",
            phone:"",
            phoneError:"",
            message:"",
            messageError:"",
            successMsg:"",
            submittedForm:false,
        }
    }

    change = event => {
      this.setState({
        [event.target.name]: event.target.value
      });
    };

    validate = () => {
      let isError = false;
      const errors = {
          fullNameError: "",
          emailError: "",
          phoneError: "",
          messageError: ""
      };

      // name validation
      const nameRegx = /^[a-zA-Z-,]+(\s{0,1}[a-zA-Z-, ])*$/

      if (this.state.fullName.length == "") {
          isError = true;
          errors.fullNameError = "Please enter a name";
      } else if (!nameRegx.test(this.state.fullName)) {
          isError = true;
          errors.fullNameError = "Only characters are allowed without number";
      } else if (!isNaN(this.state.fullName)) {
          isError = true;
          errors.fullNameError = "Only characters are allowed";
      }
       else if (this.state.fullName.length <= 2) {
          isError = true;
          errors.fullNameError = "Name needs to be atleast 3 characters long";
      }

      // email validation
      const regx = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/

      if (!this.state.email || (this.state.email && this.state.email.length == "")) {
          isError = true;
          errors.emailError = "Please enter a email";
      } else if (!regx.test(this.state.email)) {
          isError = true;
          errors.emailError = "That doesn't seem to be a valid email";
      }

      // phone validation
      if(isNaN(this.state.phone)){
          isError = true;
          errors.phoneError = "That doesn't seem to be a valid phone number";
      } else if(this.state.phone.length == "") {
          isError = true;
          errors.phoneError = "Please enter a phone number";
      } else if(this.state.phone.length < 10 || this.state.phone.length > 10) {
          isError = true;
          errors.phoneError = "That doesn't seem to be a valid phone number";
      }
      

      // message validation
      if (this.state.message.length == "") {
          isError = true;
          errors.messageError = "Please enter a message";
      } else if (this.state.message.length < 5) {
          isError = true;
          errors.messageError = "Invalid message";
      }

      this.setState({
          ...this.state,
          ...errors
      });

      return isError;
  };

  onSubmit = e => {
    e.preventDefault();

    const err = this.validate();

        if (!err) {
            this.setState({
                fullName: "",
                fullNameError: "",
                phone: "",
                phoneError: "",
                email: "",
                emailError: "",
                message: "",
                messageError: "",
                successMsg: "Thank You! i will get in touch with you soon.",
                submittedForm:true
            });
            
            axios.post('https://formcarry.com/s/dmwGAqY5nIr', {
                fullName: this.state.fullName,
                phone: this.state.phone,
                email: this.state.email,
                message: this.state.message,
            } , {headers: {"Accept": "application/json"}})
            .then(function (response) {
                console.log("form submitted");  
                console.log(response);
            })
            .catch(function (error) {
                console.log("error in form submission");  
                console.log(error);
            });
        } else {
            this.setState({
                successMsg:""
            });
        }
    };

    anotherReq = () =>{
        return(
            this.setState({ submittedForm:false })
        )
    }

  render() {

    const Cntct_form = () =>{
      return(
        <form onSubmit={ this.onSubmit } className="form__width">
          <input 
            type="text" 
            name="fullName" 
            onChange={ this.change } 
            className="contact__input" 
            placeholder="Name"
            title="akshat soni"
           />
          <input type="text" name="phone"
           onChange={ this.change }
           className="contact__input" placeholder="Mobile"
           title="akshat soni"
           />
          <input type="text" name="email" 
            onChange={ this.change }
            className="contact__input" placeholder="Email"
            title="akshat soni"
            />
          <textarea className="contact__input" 
            onChange={ this.change }
          name="message" placeholder="Message" rows="5" cols="49"
          title="akshat soni"
          >
          </textarea>
          <input type="hidden" name="_gotcha"/>
           { 
                this.state.fullNameError !== undefined &&          this.state.fullNameError.length > 0 &&
                <div className="alert alert-danger"> 
                    <strong className="text-danger">&#9888; </strong>
                    { this.state.fullNameError }
                </div>
            }

            { 
                this.state.phoneError !== undefined &&
                this.state.phoneError.length > 0 &&
                <div className="alert alert-danger">
                    <strong className="text-danger">&#9888; </strong>
                    { this.state.phoneError }
                </div>
            }

            { 
                this.state.emailError !== undefined &&
                this.state.emailError.length > 0 &&
                <div className="alert alert-danger">
                    <strong className="text-danger">&#9888; </strong>
                    { this.state.emailError }
                </div>
            }
            
            { 
                this.state.messageError !== undefined &&
                this.state.messageError.length > 0 &&
                <div className="alert alert-danger">
                    <strong className="text-danger">&#9888; </strong>{ this.state.messageError }
                </div>
            }
            <button type="submit" className="akshat__btn">Submit</button>
        </form>
      )
    }

    const thanku = () => {
      return(
          <div>
              <div className="contactform__thanku" title="akshat soni">
                  <h1 className="contactform__tytext1" title="akshat soni">Thank you!</h1>
                  <br/>
                  <img src={ require('../../assets/images/smileface.png')} alt="akshat soni"/>
                  <br/>
                  <h2 className="contactform__tytext2" title="akshat soni">I will get in touch with you soon.</h2>
              </div>
              <button onClick={ this.anotherReq } className="akshat__btn" title="akshat soni">Another Request</button>
          </div>
      )
  }
    return (
      <div className="akshat__rtdv">
        <div className="contactDivbg">
            <div className="akshat__container">
            <h1 className="akshat__heading" title="akshat soni">Contact</h1>
            <div className="cntct__ltdv" title="akshat soni">
                <h1 className="contact__title" title="akshat soni">Get in touch</h1>
                <div className="cntct__container" title="akshat soni">
                <span className="contact__icon" title="akshat soni">
                    <FontAwesomeIcon icon={faEnvelope} />
                </span>
                <span className="contact__mail" title="akshat soni">
                    akshat.soni31@gmail.com<br/>
                    {' '}{'\u00A0'}{' '}{'\u00A0'}{' '}{'\u00A0'}{' '}{'\u00A0'}{' '}{'\u00A0'}{' '}{'\u00A0'}akshat@akshatsoni.com
                </span>
                <br/>
                <br/>
                <span className="contact__icon" title="akshat soni">
                    <FontAwesomeIcon icon={faPhoneSquare} />
                </span>
                <span className="contact__mail" title="akshat soni">
                +91-7803034440
                </span>
                </div>
            </div>
            <div className="cntct__rtdv" title="akshat soni">
                <h1 className="contact__title" title="akshat soni">Contact Form</h1>
                { this.state.submittedForm ? thanku() : Cntct_form() }
            </div>
            </div>
        </div>
      </div>
    );
  }
}

export default Contact;
